.widgetLg{
    flex: 2;
    box-shadow: 0px 0px 15px -10px black;
    padding: 20px;
}

.widgetLgTitle{
    font-size: 25px;
    font-weight: 600;
}

.widgetLgTable{
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTh{
    text-align: left;
}

.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}

.widgetLgDate, .widgetLdAmount{
    font-weight: 300;
}
.widgetLgButton{
    padding: 8px 10px;
    border: none;
    border-radius:10px;
}

.widgetLgButton.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}

.widgetLgButton.Declined{
    background-color: #fff0f1;
    color: #d95087;
}

.widgetLgButton.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;
}